import React from "react";
import AliceCarousel from "react-alice-carousel";
import { itemsData, FeedbackItemType } from "./itemsData";
import "react-alice-carousel/lib/alice-carousel.css";
import { Star } from "./images/Star";

export const Item: React.FC<{ data: FeedbackItemType }> = ({ data }) => {
    return (
        <div className="list__item">
            <div className="flex w-full items-center mb-6">
                <img
                    src={data.src}
                    alt={data.src}
                    width="64px"
                    height="64px"
                    className="max-h-16 mr-4"
                />
                <p>
                    <span className="list__item-title">
                        <strong>{data.title}</strong>
                    </span>
                    <span className="list__item-rating">
                        <strong className="pr-2">Ratings: </strong>
                        <Star />
                        <Star />
                        <Star />
                        <Star />
                        <Star />
                    </span>
                </p>
            </div>
            <p>{data.subtitle}</p>
        </div>
    );
};

const items = itemsData.map((item) => <Item key={item.title} data={item} />);

const responsive = {
    0: { items: 1, itemsFit: "contain" },
    568: { items: 2, itemsFit: "contain" },
    767: { items: 2, itemsFit: "contain" },
    1024: { items: 3, itemsFit: "contain" },
};

export const Carousel = () => {
    return (
        <AliceCarousel
            mouseTracking
            autoHeight
            autoPlay
            infinite
            items={items}
            responsive={responsive}
            disableDotsControls
            animationDuration={2000}
            animationEasingFunction={"ease-in-out"}
        />
    );
};

Carousel.displayName = "Carousel";

export default Carousel;
